.App-header {
  background-image: linear-gradient(to bottom right, #a86c3c, #5e1c0f);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.letter-link {
  color: #fcfcfc;
  margin: 20px;
}

.letter-link:hover {
  color: #afc226;
}

button {
  background-color: #ff7f7f; /* Color de fondo */
  color: white; /* Color del texto */
  border: none; /* Sin bordes */
  padding: 15px 32px; /* Espaciado interno */
  text-align: center; /* Alineación del texto */
  text-decoration: none; /* Sin decoración del texto */
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer; /* Cambia el cursor al pasar el ratón */
  border-radius: 12px; /* Bordes redondeados */
  transition-duration: 0.4s; /* Duración de la transición */
}

button:hover {
  background-color: #ff4d4d; /* Color de fondo al pasar el ratón */
  color: white; /* Color del texto al pasar el ratón */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.heart {
  width: 50px;
  height: 50px;
  animation: float 1.2s ease-in;
}

@keyframes float {
  0% { transform: translateY(0); opacity: 1; }
  100% { transform: translateY(-50px); opacity: 0; }
}


.App-body{
  border-radius: 20px;
  padding: 2px;
  background-color: #824325;
  margin-top: 100px;
  max-width: 900px;
  margin-bottom: 20px;
}


.important_text{
  font-size: 3ch;
}